.footer--margin {
  margin-bottom: 19px; }

.footer__list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: #7E7E82;
  text-align: center;
  opacity: 0.72; }

@media (min-width: 768px) {
  .footer__list {
    flex-direction: row;
    justify-content: space-between; }
  .footer__item {
    flex-basis: 33%; }
    .footer__item--first {
      text-align: left; }
    .footer__item--third {
      text-align: right; } }

@media (min-width: 1360px) {
  .footer__list {
    justify-content: flex-start;
    gap: 31px; }
  .footer__item--second {
    text-align: left; } }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-size: 14px;
  font-family: "TT Lakes Regular", sans-serif;
  line-height: 18px;
  background-color: #F8F8F8;
  overflow-x: scroll; }

.bkg {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  opacity: 0;
  transition: 1s ease; }
  .bkg--active {
    visibility: visible;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
    opacity: 0.9;
    transition: 1s ease;
    z-index: 10; }

.fixed {
  position: fixed; }

.static {
  position: static; }

.container {
  margin: 0 auto;
  padding: 0 16px; }

@media (min-width: 768px) {
  .container {
    min-width: 720px;
    padding: 0 24px; } }

@media (min-width: 1120px) {
  .container {
    min-width: 1120px;
    padding: 0 32px; }
  .wrapper {
    max-width: 1320px; }
    .wrapper--margin {
      margin-bottom: 29px; } }

@media (min-width: 1120px) {
  .bkg--active {
    width: 105%;
    overflow-x: hidden; } }

@media (min-width: 1500px) {
  .fixed {
    left: 50%;
    margin-left: -36.9%; } }

