textarea {
  color: #B5B6BC; }

.modal {
  visibility: hidden;
  position: absolute;
  padding: 0 16px;
  width: 100%;
  height: 100%;
  opacity: 0.9;
  background-color: #FFFFFF; }
  .modal--close {
    visibility: hidden;
    position: fixed;
    height: 100%;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: all 1s ease;
    opacity: 0;
    z-index: 60; }
  .modal--open {
    visibility: visible;
    position: fixed;
    height: 100%;
    top: 0;
    right: 0;
    transform: translateX(0);
    transition: all 1s ease;
    opacity: 1;
    background-color: #FFFFFF;
    z-index: 60; }
  .modal__close--view {
    margin-bottom: 44px;
    padding-top: 24px; }
  .modal__title {
    font-family: "TT Lakes Bold", sans-serif;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.6px; }
    .modal__title--view {
      margin-bottom: 29px; }
  .modal__feedback {
    display: flex;
    flex-direction: column;
    gap: 14px; }
    .modal__feedback-name, .modal__feedback-number, .modal__feedback-email, .modal__feedback-comment {
      padding: 12px 16px;
      font-family: "TT Lakes Regular", sans-serif;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #B5B6BC;
      border: 1px solid #EAEAEA;
      border-radius: 8px; }
    .modal__feedback-text {
      color: #7E7E82;
      letter-spacing: 0.2px;
      line-height: 16px; }
      .modal__feedback-text--view {
        margin-bottom: 3px; }
    .modal__feedback-link {
      color: #FF3E79; }
    .modal__feedback-submit {
      position: relative;
      align-self: flex-end;
      padding: 4px 36px 4px 23px;
      font-family: "TT Lakes Bold", sans-serif;
      font-size: 12px;
      font-weight: 700;
      line-height: 24px;
      letter-spacing: -0.15px;
      text-transform: uppercase;
      color: #FFFFFF;
      border: none;
      border-radius: 16px;
      background-color: #FF3E79; }
    .modal__feedback-submit::after {
      content: "";
      position: absolute;
      top: 2px;
      right: 7px;
      background-image: url(./static/button-icon-order.svg);
      width: 24px;
      height: 24px; }
    .modal__feedback--none {
      display: none; }

@media (min-width: 768px) {
  .modal {
    width: 440px; }
    .modal-first {
      position: fixed;
      width: 440px;
      height: 100vh;
      right: 0;
      padding: 30px;
      box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
      background-color: #FFFFFF; }
    .modal__close--view {
      position: absolute;
      top: 0;
      left: -70px; } }

@media (min-width: 1360px) {
